div.centerGradient {
    background-image: radial-gradient(at center, #FFFFFF -30%, #0e0e0e 70%);
}

ul.hoverWhite li {
    color: black;
    transition: 200ms;
}

ul.hoverWhite li:hover {
    color: white;
}

button.blackBtn {
    background-color: black;
    border-radius: 20px;
    transition: 1s;
}

table.table tr th {
    display: inline-flex;
}

table.table tr th.tableAlign {
    max-width: 300px;
    min-height: 350px;
    border: 0;
    width: 100%;
}

img.laptop {
    margin-top: 70px;
    border-radius: 50px;
    box-shadow: 2px 1px 10px 1px;
    transition: 1s;
}

.mainText {
    text-shadow: 1px 1px 10px white;
    font-size: 33.6px;
    font-weight: bold;
    margin-bottom: 10px;
    transition: 1s;
}

@media screen and (min-width: 760px) {
    .tableAlign {
        width: 50%;
    }
}

@media screen and (min-width: 1030px) {
    .tableAlign {
        width: 25%;
    }
}

@media screen and (max-width: 630px) {
    button.blackBtn {
        max-width: 300px;
        min-width: 300px;
        font-size: 20px;
    }
    img.laptop {
        width: 280px;
    }
}

@media screen and (min-width: 630px) {
    button.blackBtn {
        max-width: 400px;
        min-width: 400px;
        font-size: 26.6px;
    }
    img.laptop {
        width: 430px;
    }
    .mainText {
        font-size: 44.8px;
    }
}

@media screen and (min-width: 830px) {
    button.blackBtn {
        max-width: 600px;
        min-width: 600px;
        font-size: 40px;
    }
    img.laptop {
        width: 580px;
    }
    .mainText {
        font-size: 56px;
    }
}