div.navbar-main-div {
    width: 90%;
    transition: 1s;
}

div.navbar-main-div nav div ul li {
    background-color: rgba(0, 0, 0, 0.247);
    border: 1px solid rgba(121, 121, 151, 0.432);
    border-radius: 20px;
    width: 100%;
    margin-bottom: 10px;
    padding: 4px;
    text-align: center;
}

@media screen and (min-width: 992px) {
    div.navbar-main-div {
        width: 50%;
        max-width: 488px;
    }
    div.navbar-main-div nav div ul li {
        background-color: transparent;
        width: auto;
        margin-bottom: 0;
    }
}