.text-gray {
    color: #b7b4b4;
}

li img {
    margin-left: 5px;
}

div div div div {
    font-family: sans-serif;
}

button.contactBtn {
    background-color: #f6121c;
    color: white;
    transition: 200ms;
}

button.contactBtn:hover {
    background-color: #b80c15;
    color: rgb(216, 216, 216);
}

div li.serviceItemTitle {
    margin-bottom: 6px;
    font-size: 19.2px;
    transition: 1s;
}
div li.serviceItemInfo {
    margin-left: 37px;
    margin-right: 37px;
    font-size: 12.6px;
    transition: 1s;
}

div div.contactDiv {
    color: #6ec1e4;
    font-size: 19.2px;
    transition: 1s;
}

div div div.serviceCardTitle {
    font-weight: bold;
    font-size: 22.5px;
    transition: 1s;
}

div div div.serviceCardInfo {
    white-space: pre-wrap;
    color: #6ec1e4;
    font-size: 12.6px;
    transition: 1s;
}

div img.serviceCardImg {
    transition: 1s;
    width: 100%;
}

@media screen and (min-width: 630px) {
    div li.serviceItemTitle {
        font-size: 24px;
    }
    div li.serviceItemInfo {
        font-size: 14px;
    }
    div div.contactDiv {
        font-size: 25.6px;
    }
}

@media screen and (min-width: 830px) {
    div li.serviceItemTitle {
        font-size: 24px;
    }
    div li.serviceItemInfo {
        font-size: 14px;
    }
    div div.contactDiv {
        font-size: 32px;
    }
    div img.serviceCardImg {
        width: 230.4px;
    }
}


@media screen and (min-width: 930px) {
    div li.serviceItemTitle {
        font-size: 24px;
    }
    div li.serviceItemInfo {
        font-size: 14px;
    }
    div div.contactDiv {
        font-size: 32px;
    }
    div div div.serviceCardTitle {
        font-size: 25px;
    }
    div div div.serviceCardInfo {
        font-size: 14px;
    }
    div img.serviceCardImg {
        width: 288px;
    }
}